import {Component, ViewChild, ElementRef, AfterViewInit, OnInit} from '@angular/core';
import { Chart } from 'chart.js';
import {CovidService} from '../tab2/covid.service';
import {LoadingController} from '@ionic/angular';
@Component({
  selector: 'app-tab3',
  templateUrl: 'tab3.page.html',
  styleUrls: ['tab3.page.scss']
})
export class Tab3Page implements  OnInit{
  @ViewChild('barChart', {static: false}) barChart;

  bars: any;
  colorArray: any;
  info;
  constructor(private covidServ: CovidService, private loadingCtrl: LoadingController) {}
  ngOnInit(): void {
    this.presentLoading().then(() => {
      this.covidServ.getCountry().subscribe(data => {
        this.info = data;
      },
          error1 => console.log(error1),
          () => {
                this.loadingCtrl.dismiss();
          });
    })
  }

  ionViewDidEnter() {
    this.pieCharts();
  }
  pieCharts() {
    this.bars = new Chart(this.barChart.nativeElement, {
      type: 'pie',
      data: {
        // tslint:disable-next-line:max-line-length
        labels: ['Ariana: 31', 'Biezerte: 4', 'Gafsa: 1', 'Gabes: 4', 'Kairouan: 2', 'Kebili: 5', 'Mahdia: 3', 'Manouba: 3', 'Mednine: 12', 'Monastir: 13', 'Nabel: 2', 'Sfax: 7', 'Sousse: 13', 'Tataouine: 6', 'Tunis: 47', 'Ben Arous: 17', 'Zaghouane: 2', 'Kef: 1'],
        datasets: [
          {
            label: '# of Votes',
            data: [31, 4, 1, 4, 2, 5, 3, 3, 12, 13, 2, 7, 13, 6, 47, 17, 2, 1],
            backgroundColor: [
              'rgb(99,66,210)',
              'rgb(89,213,253)',
              'rgb(255,149,120)',
              'rgb(251,107,128)',
              'rgb(59,167,86)',
              'rgb(101,99,255)',
              'rgb(252,190,44)',
              'rgb(10,36,99)',
              'rgb(216,49,91)',
              'rgb(0,100,148)',
              'rgb(220,73,58)',
              'rgb(67,146,241)',
              'rgb(140,30,7)',
              'rgb(108,209,61)',
              'rgb(23,146,22)',
              'rgb(180,119,217)',
              'rgb(229,229,229)',
              'rgb(196,221,229)',
            ]
          }
        ]
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
      }
    });
  }


  doRefresh(event) {
    console.log('Begin async operation');

    setTimeout(() => {
      console.log('Async operation has ended');
      event.target.complete();
    }, 2000);
  }

  async presentLoading() {
    const loading = await this.loadingCtrl.create({
      message: 'Please wait...'
    });
    await loading.present();
  }
}
